@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,700;1,400;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');


body {
  a{
    text-decoration: none;
  }
  margin: 0;
  padding:0;
  *{
    font-family: 'Raleway', sans-serif;
    box-sizing: border-box;
  }
}

.header{
  display: flex;
  width:100%;
  align-items: center;
  justify-content: space-between;
  padding: 0 160px;
  height: 90px;
  @media(max-width:1200px){
    padding:0 30px;
  }
  @media(max-width:991px){
    justify-content: center;

}
  &_logo{
    max-width: 240px;
    width:100%;
    height: 100%;
    background: center / contain no-repeat url('./images/logo.png');

  }
  &_links{
    display: flex;
    align-items: center;
    @media(max-width:991px){
        display: none;
    }
  }
  &_link{
    font-size: 14px;
    line-height: 16px;
    color:#2c2c2c;
    cursor: pointer;
    padding:0 10px;
    &:hover{
      color:#9b7b4d;
      text-transform:underline;
    }
  }
  &_link-active{
    color:#9b7b4d;
    text-transform:underline;
  }
}

.first-block{
  display: flex;
  justify-content: center;
  height: 580px;
  @media(max-width:991px){
    height: auto;
  }
  &_left,&_right{
    flex-basis: 50%;
    padding:0 60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:100%;
    flex-wrap: wrap;
    height: 100%;;
  }
  &_left{
    background: center / cover no-repeat url('./images/bg-1.jpg');
    justify-content: flex-end;
    align-items: flex-end;
    @media(max-width:991px){
      display: none;
    }
  .button {  
    cursor: pointer;
    color: #2c2c2c;
    transition:  800ms;
    width:220px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;    
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    border:2px solid #2c2c2c;
    margin-right: 10px;
    margin-bottom: 163px;
  }
  .up:hover,
  .up:focus {
    box-shadow: inset 0 -3.30em 0 0 #cca876;  
    transition: 800ms;
  
  }
  }
  &_right{
    background: center / cover no-repeat url('./images/2-bg.jpeg');
    background-blend-mode: color;
    background-color:rgba(27,35,54,0.2);
    @media(max-width:991px){
      flex-basis: 100%;
      height: 100%;   
     padding: 50px 30px;
     justify-content: center;

    }
    .button {  
    cursor: pointer;
    transition:  800ms;
    width:220px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;    
    color:#cca876;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    border:2px solid #cca876;
    &:hover,
    &:focus { transition:  800ms;
      color: #fff;
    }   
    @media(max-width:991px){
      margin-left: auto;
      margin-right: auto;

    }
     margin-left: 30px;
    margin-top: 50px;
  }
  .up:hover,
  .up:focus {
    box-shadow: inset 0 -3.30em 0 0 #cca876;  
    transition: 800ms;

  }

  }
  &_title{
    font-size: 50px;
    margin-bottom: 30px;
    color:#cca876  ;
    @media(max-width:991px){
      text-align: center;

    }
  }
  &_subtitle{
    line-height: 55px;
    font-weight: 700;
    font-size: 40px;
    color:#fff;
    @media(max-width:991px){
      text-align: center;

    }
  }
}
.second-block{
  display: flex;
  justify-content: space-between;
  padding: 70px 130px;
  align-items: center;

  @media(max-width:991px){
     padding: 50px 0px;
     flex-wrap: wrap;

  }
  @media(max-width:550px){
   justify-content: center;

 }
  &_item{
    width:250px;
    padding:0 10px;
    @media(max-width:991px){
      margin-bottom: 15px;

   }
  }
  &_item-image{
    height: 65px;
    width:100%;
    margin-bottom: 20px;
  }
  .coin-bg{
    background: center / contain no-repeat url('./images/coin.svg');
  }
  .chart-bg{
    background: center / contain no-repeat url('./images/pie-chart.svg');
  }
  .hand-bg{
    background: center / contain no-repeat url('./images/hand-shake.svg');
  }
  .case-bg{
    background: center / contain no-repeat url('./images/old-fashion-briefcase-1.svg');
  }
  &_title{
    font-size: 16px;
    line-height: 16px;
    color:#4a4a4a;
    text-align: center;
    margin-bottom: 8px;
  }
  &_subtitle{
    font-size: 13px;
    line-height: 20px;
    color:#4a4a4a;    
    text-align: center;

  }
}

.third-block{
  padding:50px 160px;
  @media(max-width:1200px){
    padding: 50px 30px;
 }
  &_item{
    margin-bottom: 50px;
    &-title{
      padding-bottom:12px;
      border-bottom: 1px solid #cca876;
      margin-bottom: 50px;
      color:#4a4f55;
      font-size: 28px;
      line-height: 34px;
      text-transform: uppercase;
      text-align: center;
    }
    &-content{
      display: flex;
      @media(max-width:768px){
        flex-wrap: wrap;
     }
    }
    &_image{
      object-fit: cover;
      flex-basis: calc( 50% - 10px);
      max-width: 50%;
      height: auto;    
      margin-right: 10px;
      max-height: 570px;
      @media(max-width:768px){
        flex-basis: 100%;
        margin-right: 0px;
        max-width: unset;
        max-height:300px;
        margin-bottom: 20px;

     }
    }
    &_text{     
      flex-basis: 50%;
      padding:0 10px;
      color: #4A4A4A;
      font-size:18px;
      line-height: 28px;
      @media(max-width:768px){
        flex-basis: 100%;
        margin-bottom: 20px;
     }
    }
  }
}

.footer{
  display: flex;
  padding: 50px 160px;
  background-color: #1B2336;
  @media(max-width:1200px){
    padding: 50px 30px;
 }
  &_left,&_right{
    flex-basis: 50%;
  }
  &_left{
    padding-right:15px;
  }

  &_phone{
    color:#ffffff;
    font-size:16px;
    line-height: 18px;
    margin-bottom: 5px;
   
  div{
      font-size:28px;
      line-height: 34px;
      color:#cca876;
      margin-top:10px; 
      font-family: 'Roboto', sans-serif;
    }
  }
  &_text{
    color:#d8d8d8;
    font-size: 12px;
    line-height: 20px;
  }
  &_right{
    padding-left:15px;
    flex-direction: column;
    justify-content: flex-end;
  }
  
  &_title{
    font-size: 18px;
    margin-bottom: 10px;
    color:#fff;
  }
  &_subtitle{
    font-size: 16px;
    margin-bottom: 10px;
    color: #d8d8d8;
  }
  &_link{
    &:hover{
      color:#9b7b4d;
    }
  }
}
.lang{
  display: flex;
  cursor: pointer;
  &_button{
    cursor: pointer;
    margin-right: 10px;
    font-size: 18px;
    &:hover{
      color:#cca876;
    }
  }
  @media(max-width: 991px){
    margin-left: 10px;
    position: absolute;
    right: 30px;
    top:32px; }
}